<template>
  <div id="app">
        <div class="card2">
                <!-- <h2>{{ $route.query }}</h2> -->
               <div class="card2_top">
                 <el-form :inline="true" :model="form" class="demo-form-inline">
                  <div style="float: left; margin-left:10%; margin-top: 20px;">
                      <el-form-item label="关键字" >
                          <el-input v-model="form.keyword" placeholder="请输入关键字" id="btn0"></el-input>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="primary" @click='search()'>查询</el-button>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="info" @click='reback'>重置</el-button>
                        </el-form-item>
                  </div>
                   <div style="float: right; margin-right:10%; margin-top: 20px;">
                      <el-form-item>
                          <el-button type="success" @click='add()'><router-link to='./anliku'> +新增</router-link></el-button>
                        </el-form-item>
                   </div>
                 </el-form>
                 </div>
               </div>

<!--    修改框-->
    <div class="content3" v-show="isshow2">
      <div class="add">
        <div class="header">
          <span style="float: left;" >新增案例库</span>
          <span style="float: right; cursor: pointer;"  @click='xiugai2'>X</span>
        </div>
        <div class="zhong" style="margin-top: 50px; margin-left: 100px; text-align: left;  width: 500px;">
          <el-form :inline="true" :model="form" class="demo-form-inline">
            <el-form-item  label="关键字" >
              <el-input style="width: 350px;" v-model="form.keyword2" placeholder="请输入" ></el-input>
            </el-form-item>
          </el-form>

        </div>
        <div class="bottom" style="text-align: center; margin-top: 30px;" >
          <el-row>
            <el-button type="primary" @click='xiugai2(),refrashData()'>修改</el-button>
          </el-row>
        </div>
      </div>
    </div>

<!--    ///////////////////////////////////////////-->
               <div id="card2_bottom">
                 <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
                   @selection-change="handleSelectionChange" >
                   <el-table-column prop="keyword" label="关键字">
                   </el-table-column>
                   <el-table-column prop="materialUrl" label="附件">
                   </el-table-column>
                   <el-table-column label="操作">
                     <template slot-scope="scope">
                       <el-button @click="handleClick(scope.row),xiugai2()" type="text" size="small" :data='list.id' >修改
                       </el-button>
                       <el-button @click="handleClick2(scope.row),Show()" type="text" size="small" >删除
                       </el-button>
                     </template>
                   </el-table-column>
                 </el-table>
                 <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
                   :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
                   @size-change="handleSizeChange">
                 </el-pagination>
               </div>
            </div>
</template>

<script>
import axios from 'axios'
export default {
  name: '',
  data() { 
    return {
        isShow:false,
        isshow2:false,
        
          form: {
            name: '',
            region: '',
          },
          formInline: {
            user: '',
            region: ''
          },
        powerPlantType: '',
        generatorCapacity: '',
        voltageLevel: '',
        province: '',
        form: {
          keyword: '',
          keyword2:'',
          address: '',
          phone: '',
          postcode: '',
        },
           caseLibraryID:'',
           keyword:'',
        materialUrl:'',


        msg: 8888,
        list3:[

        ],
        list2:[
          
        ],
        list:[

        ],
        pagesize: 5,
        currpage: 1
      }
  },
  created(){
    axios.get('http://localhost:8084/caseLibrary/findall')

      .then(response => {
           // alert(response)
        console.log(response)
        // this.list = response.data.data.filter(item => {
        //   return item.infoState == 0;
        // });
        this.list = response.data.data;
        this.list3 = response.data.data;
        this.list2 = this.list;
        // alert('请求成功！');
      })

      //获取失败

      .catch(error => {

        console.log(error);

        alert('网络错误，不能访问');

      })

  },
  methods: {
    search(){
      var obtn1 = document.getElementById('btn0').value;
      axios.get(`http://localhost:8084/caseLibrary/findByKeywordLike?keyword=${obtn1}`).then(response => {
        // alert(response)
        // this.list = response.data.data.filter(item => {
        //   return item.infoState == 0;
        // });
        this.list = response.data.data;
        this.list2 = this.list;
        // alert('请求成功！');
      }).catch( ( error ) => {
        // 【务必注意】这里的error输出的不是一个对象【error.response才是一个对象】
        console.log(error);
        if (error.response) {
          // 请求已发出，但服务器响应的状态码不在 2xx 范围内
          console.log( error.response.data );
          console.log( error.response.status );
          console.log( error.response.headers );
        } else {
          // Something happened in setting up the request that triggered an Error
          console.log( "Error", error.message );
        }
        console.log( error.config );
      } );
    },

    //修改
    handleClick(a){
      alert(a.keyword);
      this.form.keyword2 = a.keyword;
      this.caseLibraryID = a.caseLibraryID;
      this.materialUrl = a.materialUrl;


    },
    refrashData(){
      // console.log(this.guidingBookId, this.form.name)
      axios.post('http://localhost:8084/caseLibrary/update', {

        caseLibraryID:this.caseLibraryID,
        keyword:this.form.keyword2,
        // infoState:this.infoState,
        materialUrl:this.materialUrl,
      })
        .then(function (response) {
          window.location.reload();
          alert('修改成功!')

        })
        .catch(function (error) {
          console.log(error);
        });



    },
    xiugai2(){
      this.isshow2 = !this.isshow2;
    },

    handleClick2(a){

      axios.get(`http://localhost:8084/caseLibrary/delete?caseLibraryID=${a.caseLibraryID}`).then( result => {
        // 只有状态码为200,才能返回true,说明注册成功,其他状态码全部失败！



      } ).catch( ( error ) => {

        console.log(error);
        if (error.response) {
          // 请求已发出，但服务器响应的状态码不在 2xx 范围内
          alert("a")
          console.log( error.response.data );
          console.log( error.response.status );
          console.log( error.response.headers );
        } else {
          // Something happened in setting up the request that triggered an Error
          console.log( "Error", error.message );
        }
        console.log( error.config );
      } );
      alert('确认删除？');
      window.location.reload();
    },
    reback(){
      document.getElementById('btn0').value='';
      // console.log(this.list2);
      this.list = this.list3;
    },
  },
 }
</script>

<style lang="" scoped>
 .back{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    background-color:rgba(0,0,0,0.5);
    z-index: 2;
  }
  .el-input {
    width: 150px;
    height: 25px;
  }

  .el-select {
    width: 150px;
    height: 25px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }

  .el-button {
    padding: 4px 8px;
  }


  .card2 {
    text-align: center;
    width: 97%;
    margin: auto;
    background: white;
  }

  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }
  .el-form-item__label{
    width: 100px !important;
    padding: 0 !important;
  }
  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }
 html,body{
  width: 100%;
  height: 100%;
}
  #app{
    margin-top: 10px;
    width: 97%;
    height: 90%;
    margin-left: 1.5%;
    background: white;
    position: absolute;
    box-shadow: 0px 0px  1px black;
  }

  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style{
    width: 120px;
  }
 .content3{
   position: absolute;
   width: 100%;
   height: 97%;
   /* background: black; */
   background-color:rgba(0,0,0,0.5);
   z-index: 2;

 }

 .add{
   position: absolute;
   width: 650px;
   height: 260px;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   margin: auto;
   padding: 15px;
   background: white;
 }
</style>



